import { Component, Element, h, Host } from '@stencil/core'

@Component({
  tag: 'ato-row',
  styleUrl: 'row.scss',
  shadow: true,
})
export class AtoRow {
  @Element() private element: HTMLElement

  componentDidLoad() {
    this.element.outerHTML = this.element.outerHTML.replace(/ato/g, 'ion')
  }

  render() {
    return (
      <Host>
        <slot />
      </Host>
    )
  }
}
